import Track from "../components/Track";

class Sequence extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 1,
      clockOn: false,
      sequence: [
        [false, false, false, false, false, false, false, false],
        [false, false, false, false, false, false, false, false],
        [false, false, false, false, false, false, false, false],
        [false, false, false, false, false, false, false, false]
      ]
    };
  }

  componentWillUnmount() {
    clearInterval(this.stepID);
  }

  step() {
    const prev = this.state.step;
    const next = prev == 8 ? 1 : prev + 1;
    this.setState({
      step: next
    });
  }

  toggleCell(x) {
    return y => {
      let sequence = this.state.sequence;
      sequence[x][y] = !sequence[x][y];
      this.setState({
        sequence: sequence
      });
    };
  }

  toggleClock() {
    if (this.state.clockOn) {
      console.log("starting clock");
      clearInterval(this.stepID);
      this.setState({
        clockOn: true
      });
    } else {
      console.log("pausing clock");
      this.stepID = setInterval(() => this.step(), 250);
      this.setState({
        clockOn: false
      });
    }
  }

  render() {
    return (
      <div>
        <button className="button" onClick={() => this.toggleClock()}>
          start/stop
        </button>
        <br />
        <br />
        {this.state.sequence.map((track, i) => {
          return (
            <Track
              key={i}
              step={this.state.step}
              toggle={this.toggleCell(i)}
              sequence={track}
            />
          );
        })}
      </div>
    );
  }
}

export default Sequence;
